@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto&display=swap');

html, body {
    background: #222;
    height: 100%;
    margin: 0;
    color: #eee;
    font-family: "Georgia", serif;
    font-size: 18px;
}

h1, h2 {
    text-align: center;
    padding: 0 2rem;
    font-family: "Bobas Neu", sans-serif;
    text-transform: uppercase;
}

h1 {
    font-size: 2rem;
}

h2 {
    font-size: 1.2rem;
}

p {
    padding: 0 2rem;
    line-height: 1.4;
    margin-bottom: 1.6rem;
}

.page-content {
    padding-top: 2rem;
    padding-bottom: 2rem;
    overflow: hidden;
    position: relative;

    & > * {
        z-index: 1;
    }
}

.manifesto {
    font-family: "Georgia", serif;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
}

.full-width {
    text-align: center;
}

.separator {
    position: absolute;
    left: -50vw;
    top: 50vh;
    display: block;
    height: 50vh;
    width: 200%;
    transform: rotate(-30deg);
    background: rgba(255, 0, 0, 0.1);
    z-index: 0;
}

.avatar-tool {
    position: relative;
    z-index: 1;
    text-align: center;
    margin-top: 5rem;
}

.uploader {
    height: 33vh;
    background: #343434;
    display: flex;
    padding: 2rem;

    .upload-button {
        flex: 1 1 auto;
        padding: .5rem 1rem;
        border: 5px dashed #444;
        color: white;
        background: #333;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
    }
}

.avatar-renders {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
}

.avatar-renders canvas {
    width: 120px;
    height: 120px;
    pointer-events: none;
}

img {
    display: block;
}

.controls {
    list-style: none;
    padding: 1rem;
    margin: 0;
    text-align: center;

    li {
        margin-bottom: 1rem;
    }

    button {
        border: 1px solid #888;
        border-radius: 5px;
        padding: .25rem .75rem;
        background: #444;
        color: #ddd;
        cursor: pointer;
    }

    .button-primary {

    }

    .button-secondary {
        border: 0;
        background: transparent;
        padding: 0;
        font-size: .8rem;
        color: #aaa;
    }
}

@media only screen and (min-width: 812px) {
    .page-content {
        display: grid;
        grid-template-columns: 33.33% 33.33%;
        padding-top: 0;
    }

    h1 {
        grid-column: 1 / 3;
        font-size: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100vh;
    }

    .full-width {
        grid-column: 1 / 4;
        text-align: center;
        font-size: 1.4rem;
    }

    .avatar-tool {
        display: grid;
        grid-template-columns: 33.33% 33.33% 33.33%;
    }
}
