@import "https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto&display=swap";
html, body {
  height: 100%;
  color: #eee;
  background: #222;
  margin: 0;
  font-family: Georgia, serif;
  font-size: 18px;
}

h1, h2 {
  text-align: center;
  text-transform: uppercase;
  padding: 0 2rem;
  font-family: Bobas Neu, sans-serif;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.2rem;
}

p {
  margin-bottom: 1.6rem;
  padding: 0 2rem;
  line-height: 1.4;
}

.page-content {
  padding-top: 2rem;
  padding-bottom: 2rem;
  position: relative;
  overflow: hidden;
}

.page-content > * {
  z-index: 1;
}

.manifesto {
  align-items: center;
  font-family: Georgia, serif;
  font-size: 1.1rem;
  display: flex;
}

.full-width {
  text-align: center;
}

.separator {
  height: 50vh;
  width: 200%;
  z-index: 0;
  background: #ff00001a;
  display: block;
  position: absolute;
  top: 50vh;
  left: -50vw;
  transform: rotate(-30deg);
}

.avatar-tool {
  z-index: 1;
  text-align: center;
  margin-top: 5rem;
  position: relative;
}

.uploader {
  height: 33vh;
  background: #343434;
  padding: 2rem;
  display: flex;
}

.uploader .upload-button {
  color: #fff;
  background: #333;
  border: 5px dashed #444;
  border-radius: 10px;
  flex: auto;
  justify-content: center;
  align-items: center;
  padding: .5rem 1rem;
  display: flex;
}

.avatar-renders {
  justify-content: center;
  padding-top: 1rem;
  display: flex;
}

.avatar-renders canvas {
  width: 120px;
  height: 120px;
  pointer-events: none;
}

img {
  display: block;
}

.controls {
  text-align: center;
  margin: 0;
  padding: 1rem;
  list-style: none;
}

.controls li {
  margin-bottom: 1rem;
}

.controls button {
  color: #ddd;
  cursor: pointer;
  background: #444;
  border: 1px solid #888;
  border-radius: 5px;
  padding: .25rem .75rem;
}

.controls .button-secondary {
  color: #aaa;
  background: none;
  border: 0;
  padding: 0;
  font-size: .8rem;
}

@media only screen and (min-width: 812px) {
  .page-content {
    grid-template-columns: 33.33% 33.33%;
    padding-top: 0;
    display: grid;
  }

  h1 {
    height: 100vh;
    grid-column: 1 / 3;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
    display: flex;
  }

  .full-width {
    text-align: center;
    grid-column: 1 / 4;
    font-size: 1.4rem;
  }

  .avatar-tool {
    grid-template-columns: 33.33% 33.33% 33.33%;
    display: grid;
  }
}

